const fetchCount = (url) => fetch(url).then((res) => res.json());

export default (action) => {
  switch (action.type) {
    case 'INCR':
      return fetchCount('/api/incr');
    case 'DECR':
      return fetchCount('/api/decr');
    case 'RESET':
      return fetchCount('/api/reset');
    default:
      return fetchCount('/api/count');
  }
};
